import React, { useState, useEffect } from 'react';
import Menu from './components/Menu';
import Welcome from './components/Welcome';
import ArmyLists from './components/ArmyLists';
import Rules from './components/Rules';
import Faq from './components/Faq';
import Credits from './components/Credits';
import { armies } from './databases/ArmyRules';
import coreRules from './databases/CoreRules';

function App() {
  const [currentPage, setCurrentPage] = useState('Home');

  useEffect(() => {}, [currentPage]);

  return (
    <div className='d-flex'>
      <Menu armies={armies} coreRules={coreRules} setPage={setCurrentPage} />
      {currentPage === 'Home' ? <Welcome /> : ''}
      {coreRules.map((e) => (currentPage === e.name ? <Rules coreRules={coreRules} currentPage={currentPage} setPage={setCurrentPage} /> : ''))}
      {armies.map((e) => (currentPage === e.name ? <ArmyLists armies={armies} currentPage={currentPage} /> : ''))}
      {currentPage === 'FAQ' ? <Faq currentPage={currentPage} /> : ''}
      {currentPage === 'Credits' ? <Credits currentPage={currentPage} /> : ''}
    </div>
  );
}

export default App;
