import { AiOutlineArrowRight } from 'react-icons/ai';
import { motion } from 'framer-motion';

const Rules = ({ coreRules, currentPage, setPage }) => {
  return (
    <motion.div animate={{ x: 0 }} initial={{ x: 100 }} class='p-4 p-md-5 pt-5'>
      <h2>
        {currentPage} <small className='h6 text-muted'>/ Pocket Edition v.0.1.0</small>
      </h2>
      <hr />
      {coreRules.map((e, index) =>
        currentPage === e.name ? (
          <div>
            {e.text}
            <hr />
            {index < 1 ? (
              <a href='#' class='btn link-dark rounded text-muted justify-content-end w-100' onClick={() => setPage(coreRules[index + 1].name)}>
                Next Chapter: {coreRules[index + 1].name} <AiOutlineArrowRight />
              </a>
            ) : (
              <a href='#' class='btn link-dark rounded text-muted justify-content-end w-100' onClick={() => setPage('The Empire')}>
                Check out The Empire army list? <AiOutlineArrowRight />
              </a>
            )}
          </div>
        ) : (
          ''
        )
      )}
    </motion.div>
  );
};

export default Rules;
