import { motion } from 'framer-motion';

const Welcome = () => {
  return (
    <motion.div animate={{ x: 0 }} initial={{ x: 100 }} id='content' class='p-4 p-md-5 pt-5'>
      <h2>
        Welcome! <small className='h6 text-muted'>/ v.0.1.0</small>
      </h2>
      <hr />
      <h3 className='display-4 mb-3'>Warmaster Pocket</h3>
      <p>It's a pocket-sized app to help you play Warmaster.</p>
      <p>95% of the original Warmaster Revolution rules have been preserved in this version, however all the rules have been rewritten to be easier to read.</p>
      <p>This is not a replacement for the Warmaster Revolution rules.</p>
      <small>
        <p className='text-muted'>
          Let me know what you think via the{' '}
          <a className='link-secondary' href='https://discord.gg/pB2uR7kcvv' target='_blank'>
            Warmaster Revolution discord!
          </a>
        </p>
      </small>
    </motion.div>
  );
};

export default Welcome;
