import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiBootPrints, GiDeathSkull, GiSnakeSpiral, GiFlyingFlag } from 'react-icons/gi';
import { specialRules } from '../databases/ArmyRules';
import { motion } from 'framer-motion';
const iconStyle = { marginBottom: '0.2em' };

const ArmyLists = ({ armies, currentPage }) => {
  let costCount = 0;
  armies.map((e) => (e.name === currentPage ? e.units.map((f) => (f.min ? (costCount = costCount + f.min * f.points) : '')) : ''));
  return (
    <motion.div animate={{ x: 0 }} initial={{ x: 100 }} class='p-4 p-md-5 pt-5 w-100'>
      <div className='d-flex justify-content-between w-100'>
        <h2>
          {currentPage} <small className='h6 text-muted'>/ {armies.map((e) => (e.name === currentPage ? e.version : ''))}</small>
        </h2>
      </div>
      <hr />
      <p>A 2000 pts army of {currentPage} must include the following minimum army requirements:</p>
      <table className='table table-striped table-sm'>
        <thead>
          <tr>
            <th className='pl-4'>Amount</th>
            <th className='text-muted text-right pr-4'>Total Cost</th>
          </tr>
        </thead>
        <tbody>
          {armies.map((e) =>
            e.name === currentPage
              ? e.units.map((f) =>
                  f.min ? (
                    <tr>
                      <th className='pl-4'>
                        {f.min}x {f.name}
                      </th>
                      <td className='text-right pr-4'>{f.min * f.points} pts</td>
                    </tr>
                  ) : (
                    ''
                  )
                )
              : ''
          )}
        </tbody>
        <tfoot>
          <tr>
            <td className='pl-4'></td>
            <td className='text-right pr-4'>
              <span className='text-muted'>Start-up cost:</span> <strong>{costCount} pts</strong>
            </td>
          </tr>
        </tfoot>
      </table>
      <p className='text-center'>
        You have <strong>{2000 - costCount} pts</strong> left to spend on your army!
      </p>
      <p className='text-muted text-center'>
        <small>Remember, each entry below has a max-per-army restriction. <br/>Have fun!</small>
      </p>
      <h3>
        <small className='text-muted'>{currentPage} Army List</small>
      </h3>
      <hr />
      <ul class='list-unstyled list-group-flush'>
        {armies.map((e) =>
          currentPage === e.name
            ? e.units.map((f, index) => (
                <>
                  <li class='mb-1 list-group-item'>
                    <button class='btn btn-toggle align-items-center rounded collapsed d-flex justify-content-between w-100' data-toggle='collapse' data-target={'#num' + index + '-collapse'} aria-expanded='false'>
                      <span>
                        {f.name} <small className='text-muted'>/ {f.type}</small>
                      </span>
                      <span>{f.points} pts</span>
                    </button>
                    <div class='collapse' id={'num' + index + '-collapse'}>
                      <div className='p-3 pb-0 m-0 d-flex justify-content-between'>
                        <p>
                          <strong>Stats</strong> <br />
                          <small>
                            {f.attack ? (
                              <span>
                                (<GiMailedFist style={iconStyle} /> {f.attack}) <br />
                              </span>
                            ) : (
                              <span>
                                (<GiMailedFist style={iconStyle} /> â) <br />
                              </span>
                            )}
                            {f.range ? (
                              <span>
                                (<GiPocketBow style={iconStyle} /> {f.range}) <br />
                              </span>
                            ) : (
                              ''
                            )}
                            {f.hits ? (
                              <span>
                                (<GiHeartPlus style={iconStyle} /> {f.hits}) <br />
                              </span>
                            ) : (
                              ''
                            )}
                            {f.armour ? (
                              <span>
                                (<GiCheckedShield style={iconStyle} /> {f.armour}) <br />
                              </span>
                            ) : (
                              <span>
                                {f.type === 'General' || f.type === 'Hero' || f.type === 'Wizard' || f.type === 'Chariot Mount' || f.type === 'Monstrous Mount' || f.type === 'Special' ? (
                                  ''
                                ) : (
                                  <>
                                    (<GiCheckedShield style={iconStyle} /> â)
                                    <br />
                                  </>
                                )}
                              </span>
                            )}
                            {f.move ? (
                              <span>
                                (<GiBootPrints style={iconStyle} /> {f.move}) <br />
                              </span>
                            ) : (
                              ''
                            )}
                            {f.command ? (
                              <span>
                                (<GiFlyingFlag style={iconStyle} /> {f.command}) <br />
                              </span>
                            ) : (
                              ''
                            )}
                          </small>
                        </p>
                        <p className='text-muted text-right'>
                          <small>
                            {f.size === 1 ? (f.type === 'General' ? 'Single stand character.' : f.type === 'Hero' ? 'Single stand character.' : f.type === 'Wizard' ? 'Single stand character.' : 'Single stand unit.') : ''}
                            {f.size === 2 ? 'Two stand unit.' : ''}
                            {f.size === 3 ? 'Three stand unit.' : ''}
                            {f.upgrades ? (f.upgrades[0] === 'Any' ? 'Any character upgrade.' : f.upgrades[1] ? f.upgrades[0] + ' or ' + f.upgrades[1] + ' upgrade.' : f.upgrades[0] + ' upgrade.') : ''}
                          </small>
                          <br />
                          <small>{f.unique ? 'Only one per army' : 'Max ' + (f.max ? f.max : 'â') + ' per army.'}</small>
                        </p>
                      </div>
                      <div className='px-3 mt-3'>
                        {f.rules ? (
                          f.rules.map((g) =>
                            specialRules.map((h) =>
                              g === h.name ? (
                                <>
                                  <strong>
                                    {h.name} <small className='text-muted'>Special Rule</small>
                                  </strong>
                                  {h.text}
                                </>
                              ) : (
                                ''
                              )
                            )
                          )
                        ) : (
                          <p className='text-muted text-center'>{f.type === 'General' || f.type === 'Hero' || f.type === 'Wizard' ? 'This character has no special rules.' : 'This unit has no special rules.'}</p>
                        )}
                      </div>
                    </div>
                  </li>
                </>
              ))
            : ''
        )}
      </ul>
      {armies.map((e) =>
        currentPage === e.name && e.spells ? (
          <>
            <h3>
              <small className='text-muted'>{currentPage} Spells </small>
            </h3>
            <hr />
            <ul class='list-unstyled list-group-flush'>
              {e.spells.map((f, index) => (
                <>
                  <li class='mb-1 list-group-item'>
                    <button class='btn btn-toggle align-items-center rounded collapsed d-flex justify-content-between w-100' data-toggle='collapse' data-target={'#spell' + index + '-collapse'} aria-expanded='false'>
                      <span>
                        {f.name} <small className='text-muted'>/ {f.range ? f.range : 'â'}</small>
                      </span>
                      <span>Cast on a {f.roll}+</span>
                    </button>
                    <div class='collapse' id={'spell' + index + '-collapse'}>
                      <div className='p-3 pb-0 m-0'>
                        <p>{f.text}</p>
                      </div>
                    </div>
                  </li>
                </>
              ))}
            </ul>
          </>
        ) : (
          ''
        )
      )}
      {currentPage === 'Dwarfs' ? (
        <>
          <hr />
          <p className='text-muted text-center'>
            <small>This army does not use magic.</small>
          </p>
        </>
      ) : (
        ''
      )}
    </motion.div>
  );
};

export default ArmyLists;
