import { AiOutlineMenu } from 'react-icons/ai';

const Menu = ({ armies, coreRules, setPage }) => {
  const closeDropdown = () => {
    document.getElementById('rules-collapse').classList.remove('show');
    document.getElementById('armies-collapse').classList.remove('show');
  };

  const closeSidebar = () => {
    document.getElementById('sidebar').classList.toggle('active');
  };

  return (
    <div id='sidebar' className='text-dark pt-3 active'>
      <div className='custom-menu'>
        <button type='button' id='sidebarCollapse' className='h-100 btn btn-dark' onClick={() => closeSidebar()}>
          <AiOutlineMenu />
          <span className='sr-only'>Toggle Menu</span>
        </button>
      </div>
      <div class='flex-shrink-0 p-3 bg-white' style={{ width: '250px' }}>
        <a href='/' class='h2 d-flex p-3 mb-3 link-dark text-decoration-none border-bottom'>
          WMR-P
        </a>
        <ul class='list-unstyled'>
          <li class='mb-1'>
            <button
              class='btn btn-toggle rounded w-100 btn-dark'
              onClick={() => {
                setPage('Home');
                closeSidebar();
              }}
            >
              Home
            </button>
          </li>
          <li class='mb-1'>
            <button class='btn btn-toggle rounded collapsed w-100 btn-dark' data-toggle='collapse' data-target='#rules-collapse' aria-expanded='false'>
              Rules
            </button>
            <div class='collapse' id='rules-collapse'>
              <ul class='btn-toggle-nav list-unstyled fw-normal pb-1 small'>
                {coreRules.map((e) => (
                  <li className='nav-link'>
                    <a
                      href='#'
                      class='btn link-dark rounded text-muted w-100'
                      onClick={() => {
                        closeDropdown();
                        setPage(e.name);
                        closeSidebar();
                      }}
                    >
                      {e.name}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </li>
          <li class='mb-1'>
            <button class='btn btn-toggle align-items-center rounded collapsed w-100 btn-dark' data-toggle='collapse' data-target='#armies-collapse' aria-expanded='false'>
              Armies
            </button>
            <div class='collapse' id='armies-collapse'>
              <ul class='btn-toggle-nav list-unstyled fw-normal pb-1 small'>
                {armies.map((e) => (
                  <li className='nav-link'>
                    <a
                      href='#'
                      class='btn link-dark rounded text-muted w-100'
                      onClick={() => {
                        closeDropdown();
                        setPage(e.name);
                        closeSidebar();
                      }}
                    >
                      {e.name} {e.version === 'WMRC 2021 FAQ' ? <sup className='text-danger'>New</sup> : ''}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </li>
          <li class='mb-1'>
            <button
              class='btn btn-toggle align-items-center rounded w-100 btn-dark'
              onClick={() => {
                setPage('FAQ');
                closeSidebar();
              }}
            >
              FAQ
            </button>
          </li>
          <li class='border-top my-3'></li>
          <li class='mb-1'>
            <button
              class='btn btn-toggle align-items-center rounded collapsed w-100 '
              data-bs-toggle='collapse'
              data-bs-target='#account-collapse'
              aria-expanded='false'
              onClick={() => {
                setPage('Credits');
                closeSidebar();
              }}
            >
              Credits
            </button>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Menu;
